body {
  box-sizing: border-box;
  color: #1e1823;
  padding: 20px;
  font-family: Roboto, sans-serif;
  line-height: 1.5;
}

.cat-info {
  gap: 20px;
  padding-top: 20px;
  display: flex;
}

.breed-select {
  display: block;
}

.header {
  color: #fd9222;
}

.pretty-loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fd9222;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 20px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.visually-hidden {
  display: none;
}

